.outer-container {
  display: flex;
  min-height: 100%;
  flex: 1 1 0%;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 1rem;
  margin-top: -1rem;
}

@media (min-width: 640px) {
  .outer-container {
    aspect-ratio: 4 / 3;
  }
}

@media (min-width: 768px) {
  .outer-container {
    aspect-ratio: 2 / 1;
  }
}

.step-circle-container {
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  max-width: 28rem;
  border-radius: 2rem;
  box-shadow:
    0 20px 25px -5px rgba(0, 0, 0, 0.1),
    0 10px 10px -5px rgba(0, 0, 0, 0.04);
  background-color: #141414;
  border: 1px solid #4b5563;
}

.step-indicator-row {
  display: flex;
  width: 100%;
  align-items: center;
  padding: 2rem;
}

.step-content-default {
  position: relative;
  overflow: hidden;
  pointer-events: auto;
}

.step-default {
  padding-left: 2rem;
  padding-right: 2rem;
  pointer-events: auto;
}

.footer-container {
  padding-left: 2rem;
  padding-right: 2rem;
  padding-bottom: 2rem;
  position: relative;
  z-index: 10;
}

.footer-nav {
  margin-top: 2.5rem;
  display: flex;
  position: relative;
  z-index: 10;
}

.footer-nav.spread {
  justify-content: space-between;
}

.footer-nav.end {
  justify-content: flex-end;
}

.back-button {
  transition: all 350ms;
  border-radius: 0.25rem;
  padding: 0.25rem 0.5rem;
  color: #a3a3a3;
  cursor: pointer;
  border: none;
  background: none;
  font-size: 1rem;
  z-index: 10;
  position: relative;
  pointer-events: all !important;
}

.back-button:hover {
  color: #52525b;
}

.back-button.inactive {
  pointer-events: none !important;
  opacity: 0.5;
  color: #a3a3a3;
}

.next-button {
  transition: all 350ms;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 9999px;
  background-color: #dc2626;
  color: #fff;
  font-weight: 500;
  letter-spacing: -0.025em;
  padding: 0.375rem 0.875rem;
  cursor: pointer;
  border: none;
  z-index: 10;
  position: relative;
  pointer-events: all !important;
}

.next-button:hover {
  background-color: #b91c1c;
}

.next-button:active {
  background-color: #991b1b;
}

.step-indicator {
  position: relative;
  cursor: pointer;
  outline: none;
}

.step-indicator-inner {
  display: flex;
  height: 2rem;
  width: 2rem;
  align-items: center;
  justify-content: center;
  border-radius: 9999px;
  font-weight: 600;
}

.active-dot {
  height: 0.75rem;
  width: 0.75rem;
  border-radius: 9999px;
  background-color: #fff;
}

.step-number {
  font-size: 0.875rem;
}

.step-connector {
  position: relative;
  margin-left: 0.5rem;
  margin-right: 0.5rem;
  height: 0.125rem;
  flex: 1;
  overflow: hidden;
  border-radius: 0.25rem;
  background-color: #52525b;
}

.step-connector-inner {
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
}

.check-icon {
  height: 1rem;
  width: 1rem;
  color: #fff;
}

/* Input styles for stepper */
.step-default input {
  width: 100%;
  padding: 0.75rem;
  margin-top: 0.5rem;
  border: 1px solid #374151;
  border-radius: 0.375rem;
  font-size: 1rem;
  font-family: inherit;
  transition: all 200ms;
  box-sizing: border-box;
  background-color: #141414;
  color: #fff;
}

.step-default input:focus {
  outline: none;
  border-color: #dc2626;
  box-shadow: 0 0 0 3px rgba(220, 38, 38, 0.1);
}

.step-default input::placeholder {
  color: #6b7280;
}

.step-default textarea {
  width: 100%;
  padding: 0.75rem;
  margin-top: 0.5rem;
  border: 1px solid #374151;
  border-radius: 0.375rem;
  font-size: 1rem;
  font-family: inherit;
  transition: all 200ms;
  box-sizing: border-box;
  background-color: #141414;
  color: #fff;
  min-height: 120px;
  resize: vertical;
}

.step-default textarea:focus {
  outline: none;
  border-color: #dc2626;
  box-shadow: 0 0 0 3px rgba(220, 38, 38, 0.1);
}

.step-default textarea::placeholder {
  color: #6b7280;
}

/* General text styling in steps */
.step-default h2 {
  margin: 0 0 1rem 0;
  font-size: 1.5rem;
  font-weight: 600;
  color: #fff;
}

.step-default p {
  margin: 0.5rem 0;
  color: #e5e7eb;
  line-height: 1.5;
}
